.App {
  text-align: center;
}

*, *:after,*:before {
  box-sizing: border-box;
}

body {
  width:100%;
}

a {
  text-decoration: none;
  color:black
}

li {
  list-style: none;
}

.top li {
  display: inline;
  margin-right: 10px;
  font-size: 0.8em;
}

.top li a {
  color: #000;
}

.top li a:hover {
  font-weight: 800;
}

#ri {
  margin-right: -70%;
}

.header h1 a {
  color: rgb(82, 75, 74);
  text-align: center;

}

.main img {
  width: 100%;
  height: 100%;
}

.sub h1 {
  width: 100%;
  height: 50px;
  transform: translateY(50%);
}

.sub {
  display: inline-flex;
  width: 80%;
  margin: 0 auto;
}

.sub > li {
  width: 25%;
  margin: 20px 5px;
}

.sub li img {
  width: 100%;
}


.main_sub img {
  width: 100%;
  height: 100%;
}

.new {
  border-bottom: 3px solid #131313;
  width: 60%;
  margin: 0 auto;
  margin-bottom: 20px;
}

.S_sub {
  display: inline-flex;
  width: 80%;
  margin: 0 auto;
}

.S_sub > li {
  width: 25%;
  margin: 20px 5px;
}

.S_sub li img {
  width: 100%;
}

.footer {
  width: 100%;
  padding: 100px 0;
  background-color: #000;
  color: #fff;
  font-size: 1.8rem;
}
